* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: "Poppins", sans-serif;
}

#root {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background-color: rgb(30, 41, 30);
}

.box {
    background-color: #fff;
    padding: 2rem;
    width: 600px;
    max-width: 100%;
    text-align: center;
    border-radius: 8px;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.2);
}

.box h3 {
    margin-top: 0;
}

.box p {
    margin: 2rem;
}

.box button {
    cursor: pointer;
    background-color: rgb(30, 41, 30);
    border: 0;
    color: #fff;
    border-radius: 4px;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.2);
    padding: 0.5rem 1rem;
    transition: transform 0.2s ease-in;
    margin: 10px;
}

.box button:active {
    transform: scale(0.98);
}

.box button:focus {
    outline: none;
}